<template>
    <div class="searchPage">
      <!-- 标题 -->
      <van-nav-bar title="商品搜索" left-arrow  @click-left="$router.back()"></van-nav-bar>
      <!-- 搜索栏 -->
      <van-search v-model.trim="search" shape="round" show-action placeholder="请输入搜索关键词" >
        <template #action>
        <div @click="addHistoryList(search)">搜索</div>
      </template>
      </van-search>
      <!-- 搜索历史 -->
      <div class="search-history" v-if="historyList.length>0">
        <div class="title">
          <span>最近搜索</span>
          <van-icon @click="clearHistoryList" name="delete-o" size="20" />
        </div>
      </div>
      <!-- 历史清单 -->
      <div class="list">
        <div class="list-item" v-for="item in historyList" :key="item" @click="addHistoryList(item)" >{{item}}</div>
      </div>
    </div>
</template>

<script>
import { setHistoryList, getHistoryList } from '@/utils/storage'
export default {
  name: 'SearchListIndex',
  data () {
    return {
      search: '',
      historyList: getHistoryList()
    }
  },
  methods: {
    addHistoryList (key) {
      if (key === '') {
        return
      }
      const num = this.historyList.indexOf(key)
      if (num !== -1) {
        this.historyList.splice(num, 1)
      }
      this.historyList.unshift(key)
      this.search = ''
      setHistoryList(this.historyList)
      this.$router.push(`/searchList?search=${key}`)
    },

    clearHistoryList () {
      this.historyList = []
      setHistoryList([])
    }
  }
}
</script>

<style scoped lang="less">
.van-search__action{
  background-color: red;
  color: white;
  border-radius: 0 10px 10px 0;
  width: 75px;
  text-align: center;
}
.title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
.list{
display: flex;
flex-wrap: wrap;
justify-content: start;
}
.list-item{
  text-align: center;
  border: 1px solid #d6d4d4;
  width: 30%;
  height: 30px;
  line-height: 30px;
  margin: 5px 3px;
  border-radius: 15px;
}
</style>
